.picker-wrapper{position:absolute;z-index:1001;bottom:0;width:100%}.picker-container{position:relative;z-index:1002;width:100%;margin:0 auto;background-color:#fff;box-shadow:0 2px 8px rgba(0,0,0,0.5);-webkit-transition:all .3s ease;transition:all .3s ease}.picker-header{padding:0;font-size:1.6rem;background:#F8F8F8;border-bottom:1px solid #ddd}.picker-header button{float:left;width:100px;height:40px;border:0;background:transparent}.picker-header .btn-confirm{float:right;color:#41B883}.picker-body{display:-webkit-box;display:-ms-flexbox;display:flex;box-align:center;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;box-orient:horizontal;box-direction:normal}.picker-item{-webkit-box-flex:1;-ms-flex:1;flex:1;min-width:20px;width:0}.scroller-component{display:block;position:relative;height:238px;overflow:hidden;width:100%}.scroller-content{position:absolute;left:0;top:0;width:100%;z-index:2}.scroller-mask{position:absolute;left:0;top:0;height:100%;margin:0 auto;width:100%;z-index:3;background-image:-webkit-linear-gradient(rgba(255,255,255,0.95), rgba(255,255,255,0.6)),-webkit-linear-gradient(bottom, rgba(255,255,255,0.95), rgba(255,255,255,0.6));background-image:linear-gradient(rgba(255,255,255,0.95), rgba(255,255,255,0.6)),linear-gradient(to top, rgba(255,255,255,0.95), rgba(255,255,255,0.6));background-position:top,bottom;background-size:100% 102px;background-repeat:no-repeat}.scroller-item{text-align:center;font-size:14px;height:34px;line-height:34px;color:#000;line-clamp:1;-webkit-line-clamp:1;overflow:hidden;text-overflow:ellipsis}.scroller-indicator{width:100%;height:34px;position:absolute;left:0;top:102px;z-index:1;background:#f8f8f8;background-image:-webkit-linear-gradient(top, #d0d0d0, #d0d0d0, transparent, transparent),-webkit-linear-gradient(bottom, #d0d0d0, #d0d0d0, transparent, transparent);background-image:linear-gradient(to bottom, #d0d0d0, #d0d0d0, transparent, transparent),linear-gradient(to top, #d0d0d0, #d0d0d0, transparent, transparent);background-position:top,bottom;background-size:100% 1px;background-repeat:no-repeat}.scroller-item-selected{font-size:14px}


